.list-section .list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.list-section .list-header .orders-count {
  font-size: 1rem;
  font-weight: 500;
  color: #374151;
}
.list-section .list-header .view-toggle {
  display: flex;
  gap: 0.5rem;
  background: #f3f4f6;
  border-radius: 8px;
  padding: 0.25rem;
}
.list-section .list-header .view-toggle .view-btn {
  background: none;
  border: none;
  padding: 0.5rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #6b7280;
}
.list-section .list-header .view-toggle .view-btn:hover {
  color: #374151;
}
.list-section .list-header .view-toggle .view-btn.active {
  background: white;
  color: #2563eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}
.list-section .list-header .view-toggle .view-btn i {
  font-size: 0.875rem;
}
.list-section .work-orders-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
}
.list-section .work-orders-grid.list-view {
  grid-template-columns: 1fr;
}
.list-section .work-orders-list {
  display: none;
  flex-direction: column;
  gap: 1rem;
}
.list-section .work-orders-list.active {
  display: flex;
}
.list-section .work-orders-grid.active {
  display: grid;
}

@media (max-width: 768px) {
  .list-section .list-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }
  .list-section .list-header .view-toggle {
    align-self: center;
  }
  .list-section .work-orders-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}
